
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  position: relative;
  width: 100vw;
  height: 100vh;
  min-width: 540px;
  min-height: 440px;
  font-family: "pokemon font", sans-serif;
  font-size: 1.5rem;
  background-color: black;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}
#app {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 540px;
  height: 440px;
  background-color: white;
  margin: 0 auto;
  padding: 20px;
}

.battle {
  display: grid;
  grid-gap: 5px;
  grid-template-rows: 130px 130px 140px;
  grid-template-columns: 240px 20px 240px;
  grid-template-areas: "gauge1 gauge1 monster1" "monster2 gauge2 gauge2" "serif serif serif";
}

.gauge-enemy {
  grid-area: gauge1;
  height: 100%;
  position: relative;
}
.gauge-enemy .gauges {
    width: 95%;
    height: 70%;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 30px 1fr;
}
.gauge-enemy .gauges .gauge {
      position: relative;
      border-bottom: 3px solid #333;
      border-radius: 0 0 0 15px;
      border-left: 10px solid #333;
}
.gauge-enemy .gauges .gauge:after {
        content: "";
        position: absolute;
        bottom: -3px;
        right: -10px;
        border-top: 5px solid transparent;
        border-left: 10px solid #333;
        border-bottom: 5px solid #333;
        border-right: 10px solid transparent;
}

.name h2 {
  font-size: 2rem;
  font-weight: normal;
  letter-spacing: 1.2px;
  padding-left: 20px;
}
.name h2 span {
    font-size: 1.5rem;
    font-weight: bold;
}

.hps {
  margin: 0 0 0 10px;
  border-right: solid 10px #333;
  border-bottom: solid 2px #333;
  display: grid;
  grid-template-columns: 45px 1fr;
}
.hps .hp {
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
    background-color: #333;
    color: yellow;
}
.hps .hp-gauge {
    position: relative;
    top: 8px;
    height: 45%;
    background-color: green;
}

.gauge-me {
  position: relative;
  grid-area: gauge2;
  height: 100%;
}
.gauge-me .gauges {
    position: absolute;
    bottom: 0;
    width: 95%;
    height: 70%;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 30px 1fr;
}
.gauge-me .gauges .gauge {
      position: relative;
      border-bottom: 3px solid #333;
      border-radius: 0 0 15px 0;
      border-right: 10px solid #333;
}
.gauge-me .gauges .gauge:after {
        content: "";
        position: absolute;
        bottom: -3px;
        left: -10px;
        border-top: 5px solid transparent;
        border-left: 10px solid transparent;
        border-bottom: 5px solid #333;
        border-right: 10px solid #333;
}

.enemy-enter-active, .enemy-leave-active {
  -webkit-transition: opacity 1s, -webkit-transform .5s;
  transition: opacity 1s, -webkit-transform .5s;
  transition: transform .5s, opacity 1s;
  transition: transform .5s, opacity 1s, -webkit-transform .5s;
}
.enemy-enter, .enemy-leave-to {
  -webkit-transform: scale(0, 0);
          transform: scale(0, 0);
  opacity: 0;
}
.monster-enemy {
  grid-area: monster1;
  width: 70%;
  height: 100%;
  margin: 0 auto;
}
.monster-enemy img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.me-enter-active, .me-leave-active {
  -webkit-transition: opacity .8s, -webkit-transform .5s;
  transition: opacity .8s, -webkit-transform .5s;
  transition: transform .5s, opacity .8s;
  transition: transform .5s, opacity .8s, -webkit-transform .5s;
}
.me-enter, .me-leave-to {
  -webkit-transform: scale(0, 0);
          transform: scale(0, 0);
  opacity: 0;
}
.monster-me {
  grid-area: monster2;
  width: 60%;
  height: 100%;
  margin: 0 auto;
  border-radius: 50%;
  background-color: black;
}
.monster-me img {
    opacity: .75;
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    border-radius: 50%;
}

.serif {
  grid-area: serif;
  position: relative;
  height: 100%;
  padding: 2px;
  border-radius: 10px;
  border: solid 2px #333;
}
.serif .wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    border: solid 2px #333;
    border-top: solid 5px #333;
    border-bottom: solid 5px #333;
}
.serif .wrapper .content {
      position: absolute;
      bottom: 0;
      padding: 5px 20px;
      font-size: 2.4rem;
      line-height: 2.0;
}
.serif .wrapper .content a {
        text-decoration: underline;
}
.serif .wrapper .content a:hover {
          color: blue;
}

.command {
  position: absolute;
  top: -5px;
  right: -2px;
  width: 250px;
  height: 150px;
  padding: 2px;
  border-radius: 10px;
  border: 2px solid #333;
  -webkit-box-shadow: 0 0 10px #fff;
          box-shadow: 0 0 10px #fff;
  background-color: #fff;
}
.command .wrapper {
    display: grid;
    padding: 20px 10px;
    font-size: 2.4rem;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
}

@-webkit-keyframes flash {
50% {
    opacity: 0;
}
}
@keyframes flash {
50% {
    opacity: 0;
}
}
.cmd:hover {
  -webkit-animation: flash .7s ease-in-out infinite;
          animation: flash .7s ease-in-out infinite;
  cursor: pointer;
}

@-webkit-keyframes flash {
50% {
    opacity: 0;
}
}
@keyframes flash {
50% {
    opacity: 0;
}
}
.serif-next {
  position: absolute;
  bottom: -20px;
  right: 20px;
  border: 20px solid transparent;
  border-top: 20px solid #333;
  -webkit-animation: flash 1s ease-in-out infinite;
          animation: flash 1s ease-in-out infinite;
}
.serif-next:hover {
    cursor: pointer;
}

@-webkit-keyframes flash {
50% {
    opacity: 0;
}
}
@keyframes flash {
50% {
    opacity: 0;
}
}
.item:hover {
  -webkit-animation: flash .7s ease-in-out infinite;
          animation: flash .7s ease-in-out infinite;
  cursor: pointer;
}
.item a {
  height: 60px;
  padding: 5px 10px;
  display: grid;
  grid-template-columns: 50px 200px 1fr;
  grid-gap: 5px;
}
.item a img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}
.item a .item-gauge .item-hp {
    text-align: right;
    padding-right: 20px;
}
@font-face {
  font-family: "pokemon font";
  src: url(/portfolio/static/fonts/pokemon-font.0abc39e.ttf); }
